import client from '../../../clients/eventsClient';

const EVENTS = EVENT_CALENDAR.sort((a, b) => {
  return new Date(a.year, a.month - 1) - new Date(b.year, b.month - 1)
});

export default class TopNEventsBean {
  constructor(eventDate) {
    this.eventDate = eventDate;
    this.futureEvents = EVENTS.filter((item) => {
      return item.year >= eventDate.year && item.month >= eventDate.month
    });
  }

  async fetch(n) {
    const futureEvents = this.futureEvents;
    let result = [];
    while (result.length < n && futureEvents.length) {
      const futureEvent = futureEvents.pop();
      const events = await client.get(futureEvent.year, futureEvent.month);
      result = result.concat(events.filter((event) => {
        return new Date(event.startTime) > this.eventDate.toDate()
      }));
    }
    return result.slice(0, n).sort((a, b) => {
      return new Date(a.startTime) - new Date(b.startTime)
    });
  }

}
