<template>
  <section class="bg-primary" id="events">
    <div class="container">
      <div class="row mb-4">
        <div class="col-lg-8 mx-auto text-center">
          <h2 class="section-heading text-white display-3">
            {{ title }}
          </h2>
        </div>
      </div>
      <div>
        <event-item v-for="(event, index) in events" :key="index" :event="event"></event-item>
      </div>
    </div>
  </section>
</template>

<script>
  import { AsyncDataMixin } from 'vue-async-data-2';
  import parse from 'url-parse';

  import TopNEventsBean from './TopNEventsBean'
  import EventItem from './EventItem';
  import EventDate from "../../../utils/EventDate";

  const topNEventsBean = new TopNEventsBean(new EventDate());

  export default {
    mixins: [AsyncDataMixin],
    props: {
      title: {
        type: String,
        required: true
      },
      n: {
        type: Number,
        default: 10
      }
    },
    components: {
      EventItem
    },
    data() {
      return {
        events: []
      }
    },
    asyncData: {
      events() {
        return topNEventsBean.fetch(this.n)
          .catch(e => {
            reject(e)
          });
      }
    }
  }
</script>
